import React from 'react';
import './Start.scss';
import { Spring } from 'react-spring/renderprops';

class Tutorial extends React.Component {
    render() {
        return (
            <Spring
                from={{
                    width: '0%',
                    height: '0%',
                    top: '100%',
                }}
                to={{
                    width: '100%',
                    height: '100%',
                    top: '0%'
                }}
                config={{
                    duration: 1000,
                    delay: -1000
                }}
            >
                {props => (
                    <div className="logo">
                        <div className="card left" style={props}>
                            <div className="flipper">
                                <div className="f c1" style={{ fontSize: '50px' }}>
                                    <div
                                        style={{
                                            height: '80%',
                                            display: 'flex',
                                            justifyContent: 'center',
                                            alignItems: 'center',
                                            flexDirection: 'column'
                                        }}
                                    >
                                        <div style={{ height: '50%', width: '100%' }}>Tutorial</div>
                                        <h5 style={{ height: '50%', width: '100%', fontSize: '15px' }}>
                                            <div id="text-tutorial-start">
                                                Ngi chi s chn ln lt cc  c s t 1 n 20. Cc  c chn s c t mu xanh. Trng hp ang   s 9, c 2 la chn s 10 cho ngi chi i. Ngi chi chn vo bt c  s 10 no, u hin th   ng v cng im. Mi bc quay li tr i 100 im (im nh nht l 0) v   quay li hin th nn trng nh ban u.
                                        </div>
                                        </h5>
                                    </div>
                                    <a onClick={() => this.props.changeScreen(0)}>Back</a>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </Spring>
        );
    }
}

export default Tutorial;